import React, { useState, useEffect } from 'react';
import './App.css';
import { createMuiTheme, ThemeProvider } from "@material-ui/core/styles";
import * as colors from "@material-ui/core/colors";
import CssBaseline from "@material-ui/core/CssBaseline";
import CircularProgress from '@material-ui/core/CircularProgress';
import GlobalMenu from './components/GlobalMenu';
import WordPage from './pages/WordPage';
import SocialData from './context/SocialData';

function App() {
  const [currentPage, setCurrentPage] = useState<pageNames>('WordPage');
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const theme = createMuiTheme({
    palette: {
      primary: {
        main: colors.blue[800],
      },
      type: 'dark',
    },
  });

  const changePage = (val: pageNames) => {
    setCurrentPage(val);
  }

  useEffect(() => {
    const setData = async () => {
      await SocialData.setSocialQuestions();
      setIsLoading(true);
    }
    setData();
  },[]);
  
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      {
        !isLoading
        &&
        <div style={
          {
            height: '100vh',
            width: '100vw',
            display: 'table-cell',
            verticalAlign: 'middle',
            textAlign: 'center'
          }
        }>
          <CircularProgress />
          <p>now loading...</p>
        </div>
      }
      {
        currentPage === 'WordPage' && isLoading
        &&
        <WordPage />
      }
      <GlobalMenu
          currentPage={currentPage}
          changeCurrentPage= {(val) => changePage(val)}
      />
    </ThemeProvider>
  );
}

export default App;
